/* colors */
.profile {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  padding-top: 2em;
  color: #333; }
  .profile__content {
    display: flex;
    flex-direction: row-reverse;
    border-radius: 16px;
    background-color: #1292B4;
    color: white;
    width: 80%;
    padding: 1.5em; }
  .profile__photo {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 15px;
    height: 100%;
    position: relative;
    width: 300px; }
    .profile__photo:hover .profile__img {
      opacity: 0.3; }
    .profile__photo:hover .profile__photo__overlay {
      opacity: 1; }
    .profile__photo__overlay {
      transition: 0.5s ease;
      opacity: 0;
      position: absolute;
      top: 40%;
      right: 35%; }
    .profile__photo__edit {
      font-size: 0.7rem;
      padding: 8px 4px;
      background: transparent;
      border-radius: 4px;
      border-color: red;
      color: red;
      outline: none;
      cursor: pointer; }
  .profile__img {
    opacity: 1;
    width: 100%;
    height: 100%;
    height: auto;
    transition: 0.5s ease;
    border-radius: 8px;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden; }
  .profile .button {
    color: #000;
    background-color: #fff; }
    .profile .button:hover {
      color: #1292B4;
      background-color: #fff; }

@media (max-width: 760px) {
  .profile {
    background-color: #1292B4;
    padding-top: 15em; }
    .profile__content {
      width: 100%;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      border-radius: 0; }
    .profile__img {
      width: 10em;
      height: 10em;
      border-radius: 50%; } }

